/*POPUP*/
.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(37, 43, 66, 0.5);
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
  padding: 0 5%;
}

.overlay:target {
  visibility: visible;
  opacity: 1;
  z-index: 99;
}

.popup {
  background: #fff;
  border-radius: 5px;
  max-width: 630px;
  max-height: 95vh;
  position: relative;
  transition: all 5s ease-in-out;
  box-shadow: 0px 4px 44px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  padding: 30px 40px 50px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.popup .person {
  font: 40px/34px "Sen";
  color: #38A5DB;
  margin: 40px auto 25px;
  letter-spacing: unset;
  text-align: center;
}
@media only screen and (max-width: 480px) {
  .popup .person {
    font-size: 24px;
  }
}
.popup .office {
  font: 13px/18px "Open Sans";
  color: rgba(1, 18, 38, 0.5);
  letter-spacing: 0.26px;
  text-align: center;
  margin-bottom: 30px;
